/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: Nunito;
  font-size: 1.6rem;
  line-height: 1.8;
}

p {
  color: #000000;
  font-size: 2rem;
}

.contenedor {
  width: 95%;
  max-width: 130rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img,
picture {
  width: 100%;
  display: block;
}

h1, h2, h3 {
  font-family: Nunito;
  margin: 2.5rem 0;
  font-weight: 300;
  text-align: center;
}

h1 {
  font-size: 3.8rem;
}

h2 {
  font-size: 3.4rem;
}

h3 {
  font-size: 3rem;
}

h4 {
  font-size: 2.6rem;
}

.seccion {
  margin-top: 2.5rem;
  margin-bottom: 2.5rem;
}

.contenido-centrado {
  max-width: 800px;
}

.c-azulClaro {
  color: #237BBB;
}

.c-blanco {
  color: #FFFFFF;
}

/** UI **/
.header {
  padding: 1rem 0 3rem 0;
  background-color: #FFFFFF;
  box-shadow: 0 0 1rem #e1e1e1;
}
@media (min-width: 768px) {
  .header {
    position: sticky;
    top: 0;
    z-index: 100;
  }
}
@media (min-width: 1024px) {
  .header {
    padding: 0;
  }
}

.contenido-header {
  max-width: 100%;
  margin: 0 auto;
}
.contenido-header .navegacion a:hover {
  border-bottom: 0.2rem solid #004C84;
}

.titulo {
  color: #004C84;
  font-weight: 700;
  font-size: 3rem;
  border-bottom: 0.2rem solid #004C84;
  width: max-content;
}

.descripcion {
  font-family: 0.8rem;
}

.footer {
  background-color: #004C84;
  margin-bottom: 0;
}
.footer .navegacion {
  justify-content: center;
}
.footer .contenedor-footer {
  padding-top: 2.5rem;
}
.footer .copyright {
  color: #FFFFFF;
  text-align: center;
  margin-bottom: 0;
}

.barra {
  display: flex;
  flex-direction: column;
  padding-top: 3rem;
}
@media (min-width: 1024px) {
  .barra {
    flex-direction: row;
    justify-content: space-between;
  }
}
.barra .logo {
  width: 30rem;
  margin: 0 auto 2rem auto;
  border-bottom: 0.1rem solid #004C84;
}
@media (min-width: 1024px) {
  .barra .logo {
    border-right: 0.1rem solid #004C84;
    border-bottom: none;
  }
}

@media (min-width: 768px) {
  .navegacion {
    display: flex;
    align-items: center;
  }
}
.navegacion a {
  color: #237BBB;
  text-decoration: none;
  font-size: 1.8rem;
  display: block;
  text-align: center;
  font-weight: 700;
}
@media (min-width: 768px) {
  .navegacion a {
    margin-right: 3rem;
  }
  .navegacion a:last-of-type {
    margin-right: 0;
  }
}
@media (min-width: 1024px) {
  .navegacion a {
    margin-right: 6rem;
  }
}
.navegacion a.active {
  background-color: #004C84;
  color: #FFFFFF;
  border-radius: 2rem;
  padding: 0 1rem;
}
.navegacion img {
  width: 3.5rem;
  margin: 0 auto;
}

.icono {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 5rem;
  width: 5rem;
  border-radius: 100%;
}
.icono-1 {
  background-color: #68B4EB;
}
.icono-2 {
  background-color: #68EB8C;
}
.icono-3 {
  background-color: #EBAE68;
}
.icono-4 {
  background-color: #E8EB68;
}
.icono-5 {
  background-color: #5DB64F;
}
.icono-6 {
  background-color: #4489BB;
}
.icono-7 {
  background-color: #A569D4;
}
.icono-8 {
  background-color: #CA5B43;
}
.icono-9 {
  background-color: #68EB7D;
}
.icono-10 {
  background-color: #D96757;
}
.icono img {
  height: 60%;
}

@media (min-width: 768px) {
  .accordion-materias {
    display: grid;
    gap: 1rem 5rem;
    grid-template-columns: repeat(2, 1fr);
    align-items: start;
    grid-auto-rows: auto;
  }
}

.accordion-item {
  height: auto;
  overflow: hidden;
  border: none;
  margin-bottom: 2rem;
}
.accordion-item-materias {
  border-radius: 2rem !important;
  box-shadow: 0 0.3rem 0.2rem #e1e1e1;
}
.accordion-item .accordion-header {
  padding: 1rem;
  margin: 0;
  display: flex;
  align-items: center;
}
.accordion-item .accordion-header .accordion-button {
  font-size: 2rem;
  font-weight: 700;
  color: #004C84;
  background-color: transparent;
}
.accordion-item .accordion-header .accordion-button:focus {
  box-shadow: none;
}
.accordion-item .accordion-header .accordion-button[aria-expanded=true] {
  border-bottom: 0.2rem solid #e1e1e1;
}
.accordion-item .accordion-header .accordion-button::after {
  width: 2rem;
  height: 2rem;
  background-image: url("/build/img/download.svg");
  background-size: 2rem;
}

.accordion-body {
  margin-left: 2rem;
}

.accordion-subject {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.accordion-subject a:hover {
  text-decoration: underline;
}
.accordion-subject a:hover p {
  color: #004C84;
}

.form-check-input {
  width: 2rem;
  height: 2rem;
  border: 0.2rem solid #237BBB;
}

.Texto-Bienvenida {
  font-size: 2rem;
  font-weight: 800;
  width: 80%;
  margin: 0 auto;
  padding-top: 5rem;
}
@media (min-width: 768px) {
  .Texto-Bienvenida {
    font-size: 3rem;
  }
}
@media (min-width: 1024px) {
  .Texto-Bienvenida {
    font-size: 4rem;
  }
}

.descripcion-con-imagen {
  display: flex;
  flex-direction: column;
}
@media (min-width: 768px) {
  .descripcion-con-imagen {
    flex-direction: row;
    align-items: center;
  }
}
.descripcion-con-imagen .imagen-plataforma {
  width: 100%;
}

.descripcion-plataforma-texto {
  padding: 3rem;
}
.descripcion-plataforma-texto p {
  font-weight: 600;
}
@media (min-width: 1024px) {
  .descripcion-plataforma-texto p {
    font-size: 2.5rem;
  }
}

.enlaces-bienvenida {
  width: 40%;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  gap: 2rem;
}
.enlaces-bienvenida a {
  width: 100%;
  text-align: center;
  padding: 1rem;
  border-radius: 2rem;
  border: 0.1rem solid #237BBB;
  transition: filter 0.3s ease;
}
.enlaces-bienvenida a:hover {
  filter: brightness(0.8);
}
.enlaces-bienvenida a:first-of-type {
  color: #FFFFFF;
  background-color: #237BBB;
}

@media (min-width: 1024px) {
  .contenedor-contenido {
    display: flex;
    gap: 2rem;
    height: 60rem;
  }
}
.contenedor-contenido .contenido {
  background-color: #EDF3FE;
  box-shadow: 0 0.5rem 1rem #e1e1e1;
  overflow-y: auto;
  overflow-x: hidden;
  width: 100%;
  padding-bottom: 2rem;
}
.contenedor-contenido .contenido .ruta-contenido {
  display: flex;
  align-items: center;
  gap: 1rem;
  margin-bottom: 2rem;
  background-color: #EDF3FE;
  padding: 1.5rem;
}
@media (min-width: 1024px) {
  .contenedor-contenido .contenido .ruta-contenido {
    position: sticky;
    top: 0;
  }
}
.contenedor-contenido .contenido .ruta-contenido .icono-guia {
  width: 2rem;
  height: 2rem;
}
.contenedor-contenido .contenido .ruta-contenido .ruta {
  color: #004C84;
  margin: 0;
  font-size: 1.5rem;
  user-select: none;
}
.contenedor-contenido .contenido .llenado-contenido {
  padding: 0 1.5rem;
}
.contenedor-contenido .contenido-recurso {
  width: max-content;
  max-width: 100%;
  margin: 0 auto;
  padding-top: 2rem;
}
.contenedor-contenido .contenido-recurso h2 {
  font-weight: bold;
  font-size: 2.5rem;
}
.contenedor-contenido .contenido-recurso iframe {
  max-width: 100%;
}

.acordeon-contenido {
  overflow-y: auto;
  overflow-x: hidden;
  background-color: #FFFFFF;
  box-shadow: 0 0.5rem 1rem #e1e1e1;
}
@media (min-width: 1024px) {
  .acordeon-contenido {
    border-radius: 2rem;
    width: 40%;
  }
}
.acordeon-contenido .header-contenido {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-top: 2rem;
  border-bottom: 0.1rem solid #004C84;
}
@media (min-width: 768px) {
  .acordeon-contenido .header-contenido {
    flex-direction: row;
    justify-content: center;
    gap: 3rem;
    padding: 0;
  }
}
@media (min-width: 1024px) {
  .acordeon-contenido .header-contenido {
    justify-content: start;
    padding: 0 2rem;
    gap: 1rem;
  }
}
.acordeon-contenido .header-contenido .titulo-contenido {
  border-bottom: none;
}
@media (min-width: 1024px) {
  .acordeon-contenido .header-contenido .titulo-contenido {
    font-size: 2rem;
  }
}

.accordion-button-contenido {
  color: #000000 !important;
  font-weight: 400 !important;
  border-bottom: 0.05rem solid #e1e1e1;
}
.accordion-button-contenido:focus {
  border-bottom: 0.05rem solid #e1e1e1;
}

.enlace-tema-contenido {
  color: #000000;
}
.enlace-tema-contenido:hover {
  text-decoration: underline;
  color: #004C84;
}

.item-background {
  background-color: #edf3fe;
}

/* Menú lateral */
.menu-lateral {
  width: 250px;
  background-color: #f3f3f3;
  padding: 20px;
}

.materia {
  font-size: 18px;
  font-weight: bold;
  cursor: pointer;
  margin: 10px 0;
}

.preguntas {
  display: none;
  margin-left: 10px;
}

.pregunta {
  display: inline-block;
  width: 30px;
  height: 30px;
  background-color: white;
  color: black;
  border: 1px solid #ccc;
  text-align: center;
  line-height: 30px;
  margin: 5px;
  cursor: pointer;
}

/* Estado contestada */
.pregunta.contestada {
  background-color: #004c84;
  color: white;
}

/* Contenido del examen */
.contenido-examen {
  flex-grow: 1;
  padding: 20px;
}

#cronometro {
  font-size: 18px;
  margin-bottom: 20px;
}

.opciones button {
  display: block;
  width: 100%;
  margin: 10px 0;
  padding: 10px;
  font-size: 18px;
}

.alerta {
  padding: 0.5rem;
  text-align: center;
  color: #FFFFFF;
  font-weight: 700;
  text-transform: uppercase;
  margin: 1rem auto;
  width: 80%;
  opacity: 0.5;
  border-radius: 1rem;
}
.alerta.error {
  background-color: rgb(189, 7, 7);
}
.alerta.exito {
  background-color: #71B100;
}

.contenedor-login {
  display: flex;
  flex-direction: column;
  margin: 0 auto;
  justify-content: space-evenly;
}
@media (min-width: 768px) {
  .contenedor-login {
    flex-direction: row;
    align-items: center;
  }
}
.contenedor-login .contenedor-formulario {
  padding-bottom: 2rem;
  border: 0.3rem solid #e1e1e1;
  border-radius: 2rem;
  box-shadow: 0 0.3rem 0.2rem #e1e1e1;
}
@media (min-width: 1024px) {
  .contenedor-login .contenedor-formulario {
    width: 40%;
  }
}
.contenedor-login .contenedor-formulario .formulario-login {
  width: 80%;
  margin: 0 auto;
}
.contenedor-login .contenedor-formulario .formulario-login h2 {
  font-weight: 800;
  font-size: 2rem;
}
.contenedor-login .contenedor-formulario .formulario-login h2 span {
  color: #237BBB;
}
.contenedor-login .contenedor-formulario .formulario-login input {
  all: unset;
  border: none;
  border-bottom: 0.2rem solid #e1e1e1;
  width: 100%;
  margin-bottom: 3rem;
}
.contenedor-login .contenedor-formulario .formulario-login input:focus {
  border-bottom: 0.2rem solid #004C84;
}
.contenedor-login .contenedor-formulario .formulario-login input:last-of-type {
  margin-bottom: 0;
}
.contenedor-login .contenedor-formulario .formulario-login .Iniciar-sesion {
  display: block;
  width: 100%;
  padding: 1rem;
  margin-top: 2rem;
  background-color: #004C84;
  color: #F5F5F8;
  border-style: none;
  border-radius: 2rem;
  font-weight: 700;
  font-size: 2rem;
}
.contenedor-login .contenedor-formulario .formulario-login .asistencia-login {
  font-weight: 600;
  text-align: center;
}
.contenedor-login .contenedor-formulario .formulario-login .asistencia-login p {
  margin: 2rem 0 0;
}
.contenedor-login .contenedor-formulario .formulario-login .asistencia-login a {
  color: #004C84;
}
.contenedor-login .contenedor-formulario .formulario-login .olvidaste-contrasena {
  color: #237BBB;
  font-weight: 400;
}
.contenedor-login .contenedor-imagen-login img {
  width: 90%;
  margin: 0 auto;
}

.Estas-listo-para-poner-a-prueba-tus-conocimientos {
  width: 90%;
  margin: 0 auto;
  margin-top: 5rem;
}
@media (min-width: 768px) {
  .Estas-listo-para-poner-a-prueba-tus-conocimientos {
    display: flex;
    justify-content: space-evenly;
  }
}
.Estas-listo-para-poner-a-prueba-tus-conocimientos .text-container h2 {
  font-size: 3rem;
  font-weight: 800;
  color: #004C84;
}
@media (min-width: 1024px) {
  .Estas-listo-para-poner-a-prueba-tus-conocimientos .text-container h2 {
    font-size: 4rem;
  }
}
.Estas-listo-para-poner-a-prueba-tus-conocimientos .text-container p {
  font-weight: 600;
  text-align: center;
}
@media (min-width: 1024px) {
  .Estas-listo-para-poner-a-prueba-tus-conocimientos .text-container p {
    width: 70%;
    margin: 0 auto;
  }
}
@media (min-width: 768px) {
  .Estas-listo-para-poner-a-prueba-tus-conocimientos .text-container {
    width: 80%;
  }
}
.Estas-listo-para-poner-a-prueba-tus-conocimientos .text-container a {
  display: block;
  font-size: 2rem;
  width: max-content;
  text-align: center;
  margin: 0 auto;
  padding: 0.5rem 3rem;
  background-color: #004C84;
  border-radius: 1rem;
  color: #FFFFFF;
  margin-top: 3rem;
}
.Estas-listo-para-poner-a-prueba-tus-conocimientos .contenedor-imagen {
  align-self: flex-end;
}

.contenedores-inicio {
  width: 90%;
  margin: 5rem auto;
